.header{margin:20px 0 0;position:relative;padding:10px 0}.header--title{font-family:Poppins,sans-serif;text-transform:uppercase;font-size:18px;font-weight:400;color:#355c7d;border-bottom:3px solid #355c7d;margin:.5em 0 0;padding-bottom:15px}@media screen and (min-width:768px){.header--title{font-size:30px}}.header:before{content:"";position:absolute;top:0;left:0;width:76px;height:4px;background-color:#355c7d;opacity:.8}@media screen and (min-width:768px){.header:before{height:6px;width:124px}}