.navbar{display:block;position:absolute;top:0;right:0}@media screen and (min-width:768px){.navbar{position:relative;top:0;right:0}}.navbar>input{display:block;width:40px;height:32px;position:absolute;top:15px;left:-5px;cursor:pointer;opacity:0;z-index:102;-webkit-touch-callout:none}.navbar>input:checked~.navbar--hamburger>span{opacity:1;transform:rotate(45deg) translate(-2px,-1px)}.navbar>input:checked~.navbar--hamburger>span:nth-child(2){opacity:0;transform:rotate(0deg) scale(.2)}.navbar>input:checked~.navbar--hamburger>span:last-child{transform:rotate(-45deg) translateY(-1px)}.navbar>input:checked~.navbar--menu{transform:none}.navbar--hamburger>span{display:block;width:33px;height:4px;margin-bottom:5px;position:relative;top:17px;background:#355c7d;border-radius:3px;z-index:101;transform-origin:4px 0;transition:transform .5s cubic-bezier(.77,.2,.05,1),background .5s cubic-bezier(.77,.2,.05,1),opacity .55s ease}@media screen and (min-width:768px){.navbar--hamburger>span{display:none}}.navbar--hamburger>span:last-child{transform-origin:0 100%}.navbar--menu{position:fixed;z-index:100;top:0;right:0;height:100%;width:300px;max-width:100vw;padding:70px 30px;display:grid;grid-auto-rows:60px;background-color:#edf1f4;box-shadow:0 5px 15px 1px rgba(53,92,125,.5);transform-origin:0 0;transform:translate(315px);transition:transform .5s cubic-bezier(.77,.2,.05,1)}@media screen and (min-width:768px){.navbar--menu{height:auto;width:auto;max-width:100%;padding:0;position:relative;box-shadow:none;align-items:center;grid-auto-rows:auto;grid-auto-columns:1fr;grid-auto-flow:column;transform:none}}.navbar--menu>a{text-transform:uppercase;font-size:14px;font-weight:700;color:#355c7d;text-decoration:none;opacity:.45;transition:.3s;text-align:center;padding:1.5em 0;border-bottom:3px solid #355c7d}.navbar--menu>a.active,.navbar--menu>a:hover{opacity:1}